#screen-score {
  display: none;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "retro", sans-serif;
  color: white;

  h2{
    color: white;
    font-family: "retro", sans-serif;
    font-size: 3em;
    cursor: default;
  }

  #score-table {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.2em;
    border: 4px solid white;
    border-bottom: none;

    span {
      display: flex;
      justify-content: space-between;

      span {
        width: 350px;
        border-right: 4px solid white;;
      }

      label {
        width: 120px;
      }
    }
  }
  #own-score {
    font-size: 1.2em;
    border: 4px solid white;
    font-family: "retro", sans-serif;
    color: white;

    span {
      display: flex;
      justify-content: space-between;

      span {
        width: 350px;
        border-right: 4px solid white;;
      }

      label {
        width: 120px;
      }
    }
  }
}
